.cf::before,
.cf::after {
	content: '';
	display: block;
	height: 0;
	overflow: hidden
}

.cf::after {
	clear: both
}

#container {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	height: 100%;
	background: linear-gradient(-134deg, #517FA4 0%, #243949 100%)
}

.wrapper {
	max-width: 800px;
	margin: 0 auto;
	padding: 0 20px
}

#main-nav {
	display: none
}



@media screen and (min-width: 800px) {
	main .actions {
		display: flex;
		flex-wrap: wrap
	}
	main .actions div {
		float: left;
		flex: 1 1 33.33%;
		max-width: 33.33%
	}
	main .actions.position div {
		float: left;
		flex: 1 1 25%;
		max-width: 25%
	}
}

main .button {
	position: relative;
	display: block;
	padding: 18px 30px 16px;
	text-transform: uppercase;
	text-align: center;
	font-size: 16px;
	font-weight: 700;
	line-height: 1.4;
	letter-spacing: 1px;
	text-decoration: none;
	color: #243949;
	cursor: pointer;
	background: #fffce1;
	border-radius: 30px/80px;
	transition: all .1s ease
}

main .button:not(.active):hover {
	color: #d5af63
}

main .button.active {
	background: #dab977
}

